@import url("http://fonts.googleapis.com/css?family=Ubuntu:300,400");

$blue:                        blue !default;
$black:                       black !default;

$body-color:                  $black !default;

$link-hover-color:            $blue !default;

$border-radius:               0 !default;

$font-family-base:            "Ubuntu", sans-serif !default;

$paragraph-margin-bottom:     1.2rem !default;

$font-weight-normal:          200 !default;

$h1-font-size:                1.8rem !default;

$navbar-brand-font-size:      2rem !default;
$navbar-padding-y:            0 !default;
$navbar-padding-x:            0 !default;

$breadcrumb-bg:               none;
$breadcrumb-padding-x:        0 !default;
$breadcrumb-padding-y:        0 !default;

body {
  word-spacing:               0.01rem;      /* Make words a bit more sparse. */
  letter-spacing:             0.01rem;      /* Make letters a bit more sparse. */
}

/* ------------------------------------------------------------------------------------------------------------------ */
/*                                                    BEGIN TOP NAV                                                   */
/* ------------------------------------------------------------------------------------------------------------------ */
body > nav > div.container {
  padding:                    0.9rem;
  padding-bottom:             0.6rem;       /* Equal to padding from breadcrumbs to navbar bottom border. */
}

body > nav a.navbar-brand, body > nav#mb-top-nav ul.navbar-nav a.nav-link {
  color:                      $black;
  text-decoration:            none;
}

body > nav#mb-top-nav ul.navbar-nav a.nav-link {
  padding-left:               1.2rem;
}

div#mb-brand {
  padding:                    0;
  text-align:                 center;
}

div#mb-brand p {
  font-size:                  0.9rem;
  margin:                     0;
}

body > nav a:hover {
  color:                      $black;
}

body > nav a.navbar-brand {
  font-weight:                bold;
  border-bottom:              1px solid #eeeeee;
}

body > nav a.navbar-brand:hover {
  text-decoration:            none;
}

body > nav a.nav-link {
  font-size:                  1.2rem;
  padding-bottom:             0;
}

body > nav a.nav-link:hover {
  text-decoration:            underline dashed;
}
/* ------------------------------------------------------------------------------------------------------------------ */
/*                                                     END TOP NAV                                                    */
/* ------------------------------------------------------------------------------------------------------------------ */

body > main {
  margin-top:                 0.6rem;       /* Distance between breadcrumbs and top navbar */
}

body > main article > header > h1 {
  text-align:                 center;
  margin:                     0.625rem 1.25rem 1.25rem 1.25rem;
  line-height:                1.6;
}

body > footer {
  margin-top:                 4rem;         /* Give it some space from the content. */
  margin-bottom:              1rem;
  padding-top:                1rem;         /* Give it some space from the border. */
}

pre {
  padding:                    1rem;
  background-color:           #f6f6f6;
}

blockquote {
  border-left:                0.5rem solid #dddddd;
  padding-left:               1rem;
}

